/* Estilos globales */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
}

.body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-image: url(../../public/images/imgfondo.png);
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
}
  
  /* Estilos para el header */
  
  /* Estilos para el contenido */
  .content {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, .5);
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    opacity: 1;
    background-blend-mode: darken; /*Sirve para que la letra no se pierda con el fondo de la imagen*/
  }
  
  html{
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden;
  }


  h1 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  p {
    font-size: 24px;
    margin-bottom: 40px;
  }

  
  .start-button {
    background-color: rgb(211, 30, 7);
    color: rgb(2, 2, 2);
    padding: 10px 20px;
    font-size: 18px;
    border: none;
    cursor: pointer;
    text-decoration: none;
  }
  .start-button:hover {
    background-color: #09991d;
    color: rgb(2, 2, 2);
    text-decoration: none;
  }

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #000000;
    box-sizing: border-box;
  }
  
  .box {
    background-color: #5da8ca;
    width: 400px;
    padding: 30px;
    border-radius: 8px;
  }
  
  .options {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .options button {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    padding: 8px 12px;
    margin-right: 10px;
  }
  
  .options button.active {
    background-color: #686565;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    margin-left: 1em;
    margin-right: 1em;
  }
  
  .form input {
    margin-bottom: 10px;
    padding: 8px;
    border: none;
    border-radius: 4px;
    width: 100%;
  }
  
  .submit-button {
    background-color: #094bc7;
    color: #fff;
    border: none;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  
  