.button-el {
  display: inline-block;
  background-color: #e64919;
  align-items:center;
  text-align:center;
  text-decoration: none;
  width:50vh;
  height:50vh;
  padding:20px;
  box-sizing: border-box;
  cursor: pointer;
  border: 2px solid #ffffff;
  border-radius: 10px;
  margin-right: 10px;
  margin-left: 150px;
  margin-top: 150px;
  font-weight: bold;
  color:#ffffff;
  font-size: 35px;
}

