	*{
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		
	}*/
	.bodyletras {
		display: flex;
		justify-content: center;
		align-items: center;
	  }
  
	  .h1letras {
		text-align: center;
	  }
  
	  .pletras {
		text-align: center;
		margin-bottom: 20px;
	  }

	.capa{
		position: fixed;
		width: 100%;
		height: 100vh;
		/*background: rgba(0, 0, 0, 0.5);*/
		z-index: -1;
		top: 0;left: 0;
	}
	/*Estilos para el encabezado*/
	.headerletras{
		width: 100%;
		height: 100px;
		position: fixed;
		top: 0;left: 0;
		/*background-color: #6ace77;*/
		background-color: rgb(78, 179, 78);;
		font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	}
	.containerletras{
		width: 90%;
		max-width: 3000px;
		margin-left:10px;
	}
	.containerletras .btn-menuletras, .logoletras{
		float: left;
		line-height:100px;
		margin-left: 30px;
	}
	.containerletras .btn-menuletras label{
		color: #000000; /*letras y logo*/
		font-size: 25px;
		cursor: pointer;
		
	}
	#logo {
		width: 100px;
	  }
	.containerletras .menuletras{
		float: right;
		line-height: 100px;
		
	}
	.containerletras .menuletras a{
		display: inline-block;
		padding: 7px;
		line-height: normal;
		text-decoration: none;
		color: #ffffff; /*color letras encabezado*/
		transition: all 0.3s ease;
		border-bottom: 2px solid transparent;
		font-size:large;
		margin-right: 5px;
		text-align: right;
	}
	.containerletras .menuletras a:hover{
		/*border-bottom: 2px solid #ffffff;
		padding-bottom: 5px;*/
		color:blue;
	}
	/*Fin de Estilos para el encabezado*/

	/*Menù lateral*/
	#btn-menuletras{
		
		display: none;
		font-size: large;

	}
	.container-menuletras{
		position: absolute;
		background: rgba(255, 255, 255, 0.5);
		width: 100%;
		height: 100vh;
		top: 0;left: 0;
		transition: all 500ms ease;
		opacity: 0;
		visibility: hidden;
		font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	}
	#btn-menuletras:checked ~ .container-menuletras{
		opacity: 1;
		visibility: visible;
	}
	.cont-menuletras{
		width: 100%;
		max-width: 250px;
		/*background: #6ace77;*/
		background: rgb(78, 179, 78);;;
		height: 100vh;
		position: relative;
		transition: all 500ms ease;
		transform: translateX(-100%);
	}
	#btn-menuletras:checked ~ .container-menuletras .cont-menuletras{
		transform: translateX(0%);
	}
	.cont-menuletras nav{
		transform: translateY(15%);
	}
	.cont-menuletras nav a{
		display: block;
		text-decoration: none;
		padding: 20px;
		color: #000000;
		border-left: 5px solid transparent;
		transition: all 400ms ease;
	}
	.cont-menuletras nav a:hover{
		border-left: 5px solid #fffcfc;
		background: #ffffff;
	}
	.cont-menuletras label{
		position:absolute;
		right: 5px;
		top: 10px;
		color: #fff;
		cursor: pointer;
		font-size: 18px;
	}
	/*Fin de Menù lateral*/

	
  /*Canvas y botones*/
	  .canvas-container {
		width: 200px;
		height: 200px;
		border: 1px solid #000;
		margin-bottom: 20px;
	  }
  
	  .btn-containerletras {
		display: flex;
		justify-content: center;
		align-items: center;
	  }
  
	  .btnletras {
		display: inline-block;
		padding: 10px 20px;
		margin: 0 10px;
		background-color: #1B91FF;
		color: #fff;
		border: none;
		border-radius: 10%;
		text-align: center;
		cursor: pointer;
	  }
	    /*Fin del canvas y botones*/